 <template>
  <div>
    <div>
      <textarea
        name="复制内容"
        id
        cols="30"
        rows="10"
        v-model="value"
      ></textarea>
      <button
        v-clipboard:copy="value"
        v-clipboard:success="firstCopySuccess"
        v-clipboard:error="firstCopyError"
      >
        第一种方式复制
      </button>
    </div>
    <div>
      <textarea name="粘贴内容" id cols="30" rows="10"></textarea>
    </div>
  </div>
</template>
 
 <script>
export default {
  name: "",
  components: {},

  data() {
    return { value: "A simple vuejs 2 binding for clipboard.js!" };
  },

  created() {},
  mounted() {},
  methods: {
    firstCopySuccess(e) {
      console.log("copy arguments e:", e);
      alert("复制成功!");
    },
    firstCopyError(e) {
      console.log("copy arguments e:", e);
      alert("复制失败!");
    },
  },
  watch: {},
  computed: {},
};
</script>
 
